document.addEventListener("DOMContentLoaded", function() {
  // code to run after page has loaded

  /* check if navigation-bar exists (to avoid js console errors) */
  let exists = $(".navigation-bar").length ? true : false;
  /* if user scroll add is--scrolled class */
  $(window).on("scroll", function () {
    if (exists && $(window).scrollTop() > 0) {
      $(".navigation-bar").addClass("is--scrolled");
    } else if (exists) {
      /* remove is--scrolled class */
      $(".navigation-bar").removeClass("is--scrolled");
    }
  });

/*navbar on mobile calculate 100vh*/
function setMenuHeight() {
  const width = window.innerWidth;

  if (width < 992) {
    const menuGradient = document.querySelector(".menu_gradient-links-wrap");
    const windowHeight = window.innerHeight;
    const height =
      windowHeight -
      25.3 * parseFloat(getComputedStyle(document.documentElement).fontSize);

    menuGradient.style.height = `${height}px`;
  } else {
    const menuGradient = document.querySelector(".menu_gradient-links-wrap");
    menuGradient.style.height = "auto";
  }
}

// Set initial height on page load
setMenuHeight();

// Check for changes in screen size
window.addEventListener("resize", setMenuHeight);

});
